header {
    height: 100vh;
    padding-top: 3rem;
}

.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}

/*=================== CTA ==================*/

.cta{
    margin-top: 2.5rem;
    display:flex;
    gap: 1.2rem;
    justify-content: center;
}

/*=================== SOCIALS ==================*/

.header_socials{
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 0.8rem;
    margin-top: 1.2rem;
}

.header_socials > a {
    font-size: 1.5rem;
}

/*==================MEDIA QUERIES (MEDIUM DEVICES)====================*/

@media screen and (max-width: 1024px){
    header {
        height: 68vh;
    }
}

/*==================MEDIA QUERIES (SMALL DEVICES)====================*/

@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }
}